@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
    margin: 0;
    background: url(/static/media/tube-map.5abfdace.jpg) no-repeat center center fixed;
    background-size: cover;
}
header {
    background-color: rgb(0, 0, 255);
    text-align: center;
    color: rgb(255, 255, 255);
    width: 100%;
    padding: 20px;
    font-family: 'Quicksand';
    font-weight: bold;
    font-size: 40px;
    margin-bottom: 20px;
}
div[class="card"] {
    margin-right: 10px;
    margin-bottom: 10px;
    width: 400px;
}

div[class="card-header"] {
    color: rgb(255, 255, 255);
    text-align: center;
}
h1 {
  text-align: center;
  font-size: 50px;
  font-family: 'Quicksand';
  text-decoration: underline;
}

.flex-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  padding: 5px;
}
